@charset "utf-8";
@import "./Reset.css";

html, body, #root, .container{
    display:flex;
    height:100%;
    justify-content:center;
}
body{
    font-size:14px;
    line-height:1.4;
}
.loader {
    display:flex;
    width:100%;
    height:100%;
    justify-content:center;
    align-items:center;
}
.movies{
    display:flex;
    width:80%;
    padding:70px 50px 50px;
    justify-content: space-between;
    flex-wrap:wrap;
}

